<template>
	<div>
		<h3>证件查询<i>/</i><span>说明说明说明说明说明说明说明说明</span></h3>
		<div class="cardsearch">
			<div class="h3_xia_top">
				<p>
					<span>身份证号</span>
					<el-input placeholder="请输入内容"></el-input>
				</p>
				<p>
					<span>姓名</span>
					<el-input placeholder="请输入要查询的姓名"></el-input>
				</p>
				<p class="somebtn">
					<button><i class="el-icon-search"></i>查询</button>
					<button><i class="el-icon-delete"></i>清除</button>
					<button><i class="el-icon-upload2"></i>打印</button>
				</p>
			</div>
			<div class="cardsearch_xia_con">
				<img src="../../assets/idcard.png">
				<b>发证机关：</b>姓名、车牌号、手机号<br />
				<b>出生年月：</b>姓名、车牌号、手机号
			</div>
			<hr>
			<h3 class="hisjl">历史记录<span>特别提醒：只供半年内的验检记录</span></h3>
			<p class="startjs">
				<el-date-picker
			      type="date"
			      placeholder="选择开始日期"
			      :picker-options="pickerOptions0">
			    </el-date-picker>
			    -
				<el-date-picker
			      type="date"
			      placeholder="选择结束日期"
			      :picker-options="pickerOptions0">
			    </el-date-picker>
			    <button><i class="el-icon-search"></i>查询</button>
			</p>
		</div>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				pickerOptions0: {
		          disabledDate(time) {
		            return time.getTime() < Date.now() - 8.64e7;
		          }
		        },
			}
		}
	}
</script>
<style lang="less">
h3{
	padding:30px;
	font-size:16px;
	color:rgb(46,81,165);
	i{
		color:#ccc;
		padding:0 10px;
		font-weight:normal;
	}
	span{
		color:rgb(100,100,160);
		font-weight:normal;
		font-size: 12px;
	}
}
.hisjl{
	padding:30px 0;
	span{
		color:rgb(250,40,20);
		margin-left:10px;
	}
	float:left;
}
	.cardsearch{
		width:940px;
		box-sizing: border-box;
		border:1px solid #ddd;
		margin:0 auto;
		padding:30px;
		background:#fff;
		&:after{
			display: block;
			content: "";
			clear: both;
		}
		.h3_xia_top{
			p{
				display:flex;
				width:30%;
				float:left;
				span{
					flex-grow:3;
					width:80px;
					line-height: 30px;
					font-size:14px;
					text-align: right;
					font-weight:bolder;
					color:rgb(46,100,150);
				}
				input{
					margin-left: 10px;
					background:rgb(245,245,245);
					vertical-align: middle;
				}
			}
			.somebtn{
				float:right;
				button{
					width:100px;
					height:35px;
					margin-left:10px;
					color:#fff;
					background:rgb(51,80,172);
					border:0;
					border-radius:2px;
					i{
						padding-right:5px;
					}
				}
			}
		}
	}
	.cardsearch_xia_con{
		margin-top:70px;
		line-height:30px;
		font-size:14px;
		color:rgb(46,100,150);
		img{
			float:left;
			margin-right:30px;
		}
		&:after{
			clear:both;
			display:block;
			content:"";
		}
	}
	hr{
		border:0;
		height:1px;
		background:#ddd;
		margin-top:30px;
	}
	.startjs{
		float:right;
		width:520px;
		margin-top:20px;
		color:#ccc;
		button{
			width:100px;
			height:35px;
			margin-left:10px;
			color:#fff;
			background:rgb(51,80,172);
			border:0;
			border-radius:2px;
			i{
				padding-right:5px;
			}
		}
	}
</style>


